.card {
  &__image-link {
    display: block;
    margin-bottom: 32px;
  }

  &__image {
    width: 280px;
    
    transition: all 0.3s;

    @include onTablet {
      width: 100%;
    }

    @include onDesktop {
      width: 100%;
    }

    &:hover {
      transform: scale(1.1);
    }
  }

  &__paragraph {
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
  }

  &__category {
    margin-bottom: 12px;

    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
  }

  &__price {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
  }
}
