.form__field {
  display: block;
  width: 100%;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 40px;
  padding: 14px 16px;
  font-family: inherit;
  background-color: #f8f8ff;
  transition: all 0.3s;

  &--textarea {
    
    height: 148px;
    resize: none;
    border-radius: 16px;
    font-family: inherit; 
  } 

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }

  &:hover {
    border-color: #d5d7de;
  }

  &:hover::placeholder {
    color: $mainColor;
  }

  &:focus {
    border-color: $mainColor;
    outline: none;
  }

  &:focus::placeholder {
    color: $mainColor;
  }
}
