.menu {
  height: 100vh;
  padding: $headerTopPadding 0;
  background-color: $colorGreat;
  overflow-y: auto;

  @include onTablet {
    padding-top: 52px;
  }

  &__top {
    margin-bottom: 55px;

    @include onTablet {
      margin-bottom: 60px;
    }

    @include onDesktop {
      margin-bottom: 80px;
    }
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    color: $mainColor;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      border-color: $mainColor;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 700;
    text-transform: uppercase;
    color: $mainColor;
    text-decoration: none;
    
    &--phone {
      margin-bottom: 16px;

      font-size: 16px;
      line-height: 27px;
    }

    &--action {
      padding-bottom: 8px;

      border-bottom: 1px solid $mainColor;
      font-size: 12px;
      line-height: 16px;
    }
  }
}
