.icon {
  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  &--menu {
    background-image: url(../images/menu_320.svg);

    @include onTablet {
      background-image: url(../images/menu_1260.svg);
    }

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone_320.svg);
  }

  &--cross {
    grid-column-start: 3;
    
    background-image: url(../images/cross_320.svg);
  }
}
